.sb-checkboxes__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sb-checkboxes__list-item {
  margin-bottom: 0.5rem;
}

.sb-checkboxes__title {
  font-size: var(--sb-checkbox-title-font-size, var(--sbt-font-size-30));
  font-weight: var(--sb-checkbox-title-font-weight, var(--sbt-font-weight-60));
}

.sb-checkboxes__list--horizontal {
  display: flex;
  justify-content: flex-start;
}

.sb-checkboxes__list--horizontal > .sb-checkboxes__list-item {
  margin-right: 1.5rem;
}
